<template>
    <div class="flex-page">
        <van-nav-bar class="flex-full" ref="flexFull">
            <div slot="left" class="leftBar whiteNavBar">
                <div class="backIcon" @click="goBack()">
                    <img :src="isNavBar?icon.backBlack:icon.backWhite" alt="">
                </div>
                <div class="searhInputBox">
                    <div class="searchInput" @click="goToGoodsSearch()">{{searchKeyword.length > 0 ? searchKeyword : defaultWord}}</div>
                    <img class="voiceIcon" src="@/assets/images/mall/mall_icon_voice.png" alt="" v-show="isShowVoiceBtn" @click="goToSpeech">
                    <dialog-bar v-model="sendVal" type="danger" v-bind:title="recognizeResult" content="您可以说：“花生油”<br />“我想要买花生油”<br />..." v-on:cancel="clickCancel()" @danger="clickDanger()" @confirm="clickConfirm()" dangerText="Delete" ></dialog-bar>
                </div>
                <div class="searchBtn">
                    <img class="collectBtn" :src="isNavBar?(isCollect?icon.collectionBlackSel:icon.collectionBlackNor):
                    (isCollect?icon.collectionSel:icon.collectionNor)" @click="collectBtn()" alt="">
                    <div class="shopIcon" @click="goToShoppingCart()">
                        <img :src="isNavBar?icon.shopCartBlack:icon.shopCartWhite" alt="">
                        <div class="goodsNum" :class="skuTypeNum>=100?'goodsNumMax':''" v-show="skuTypeNum!=0">
                            {{skuTypeNum<=99?skuTypeNum:'99+'}}
                        </div>
                    </div>
                </div>
            </div>
        </van-nav-bar>
        <div class="post-wrapper" :class="isScrollWrapper?'postWrapper':''" ref="scrollContent" :style="{height:goodsListHeight+'px'}" @scroll="paperScroll()">
            <div class="shopBox" :style="{height:discountsNum!=0?'165px':'130px'}">
                <!--noShopBpx-->
                <div class="shopCon">
                    <div class="shopImgBox">
                        <img :src="shopInfo.shopPicUrl" alt="">
                    </div>
                    <div class="shopInfoBox">
                        <div class="shopName">{{shopInfo.shopName}}</div>
                        <div class="shopStarAndGrade">
                            <div class="shopStarBox" v-if="shopInfo.shopLevel!=0">
                                <img v-for="shopLevelItem in shopInfo.shopLevel" :key="'shopLevelItem'+shopLevelItem"
                                     src="@/assets/images/common/icon_evaluate_red.png" alt="">
                                <img v-for="noShopLevelItem in 5-shopInfo.shopLevel"
                                     :key="'noShopLevelItem'+noShopLevelItem"
                                     src="@/assets/images/common/icon_evaluate_gray.png" alt="">
                            </div>
                            <div class="gradeNum" v-if="shopInfo.shopLevel!=0" :style="useCustom?{marginTop:'4px'}:{}">{{shopInfo.shopLevel}}</div>
                            <div class="gradeNumTxt" :style="useCustom?{marginTop:'4px'}:{}" v-if="shopInfo.shopLevel==0">未评价</div>
                            <div class="saleNum" :style="useCustom?{marginTop:'4px'}:{}">月售{{shopInfo.orderCount | threshold}}单</div>
                        </div>
                        <div class="shippingFeeBox">
                            <div>起送：￥{{shopInfo.orderBaseLine}}</div>
                            <div>配送费：￥{{shopInfo.baseLineFee}}</div>
                            <div v-show="shopInfo.deliveryBaseLine!=0&&shopInfo.deliveryBaseLine!=null">满{{shopInfo.deliveryBaseLine}}包邮</div>
                        </div>
                    </div>
                </div>
                <div class="discountsBox" :style="{height:discountsNum!=0?'90px':'60px'}">
                    <div class="discountsCon" v-show="discountsNum!=0">
                        <div class="discountsList">
                            <div class="discountsItem" :class="pickupItem.isFinished==1?'discountsItemNoCut':(pickupItem.isPicked==1?'discountsItemCut':'')"
                                 v-if="pickupList.length!=0&&pickupIndex<3" v-for="(pickupItem,pickupIndex) in pickupList"
                                 :key="'pickupItem'+pickupIndex"
                                 @click="receiveBtn(pickupItem)">
                                <div class="couponMoney">￥{{pickupItem.couponTotal}}</div>
                                <div class="pullDownBtn"  v-if="pickupItem.isPicked==0&&pickupItem.isFinished!=1">领取</div>
                                <div class="pullDownBtn"  v-else-if="pickupItem.isPicked==1">已领</div>
                                <div class="pullDownBtn"  v-else-if="pickupItem.isFinished==1">抢光</div>
                            </div>
                            <div class="fullReductionItem" v-if="pickupList.length==0&&fullIndex<3"
                                 v-for="(fullItem,fullIndex) in fullReductionList" :key="'fullItem'+fullIndex">{{fullItem.actTag}}</div>
                            <div class="fullReductionItem" v-if="pickupList.length==0&&fullReductionList.length==0&&packIndex<3"
                                 v-for="(packItem,packIndex) in packList" :key="'packItem'+packIndex">{{packItem.actTag}}</div>
                        </div>
                        <div class="discountsNumBox" @click="showMaskBox">
                            <div class="discountsTxt">{{discountsNum}}个优惠</div>
                            <img src="@/assets/images/mall/icon_filter_down.png" alt="">
                        </div>
                    </div>
                </div>
            </div>
            <div class="goodsListBox">
                <van-sidebar class="sideBar" ref="leftSideBar" :style="{maxHeight:goodsListHeight+'px'}"
                             :class="isSideBarFixed?'sideBarFixed':''" v-model="activeKey">
                    <van-sidebar-item v-for="(leftClassItem,leftClassIndex) in leftClassList"
                                      :key="'leftClassIndex'+leftClassIndex" :title="leftClassItem.shopClassName"
                                      @click="switchLeftClass" />
                    <div  :style="{height:shopCartHeight+'px',width:'100%'}" title=""></div>
                    <!--                                <van-sidebar-item v-for="(leftClassItem,leftClassIndex) in leftClassList" :key="leftClassIndex" :title="leftClassItem.shopClassName" @click="switchLeftClass"/>-->
                </van-sidebar>
                <div class="classifyList" ref="sideBar" :style="{height:goodsListHeight+'px'}"
                     :class="isSideBarFixed&&leftClassFlag!=0&&leftClassFlag!=1?'classifyListFixed':''" @scroll="classifyScroll()" @touchstart="handleTouchStart"
                     @touchend="handleTouchEnd"
                     @touchmove="handleTouchMove">
                    <div id="classifyList" style=""></div>
                    <!--普通分类商品格式-->
                    <div class="normalPattern" v-if="leftClassFlag!=0&&leftClassFlag!=1">
                        <div class="classifyTitle" ref="classifyTitle">
                            <div class="classifyTxt">{{leftClassList[activeKey].shopClassName}}</div>
                            <div class="salePriceSort">
                                <div class="saleSort" @click="sortMethod=3">
                                    <div>销量</div>
                                    <div class="redLineBox" v-show="sortMethod==3"><img
                                            src="@/assets/images/mall/redLine.png" alt=""></div>
                                </div>
                                <div class="priceSortBox" @click="priceSort()">
                                    <div class="priceSort">
                                        <div class="priceSortTxt">价格</div>
                                        <img :src="sortMethod==''||sortMethod==3?icon.filter:(sortMethod==1?icon.filterUp:icon.filterDown)"
                                             alt="">
                                    </div>
                                    <div class="redLineBox" v-show="sortMethod==1||sortMethod==2"><img
                                            src="@/assets/images/mall/redLine.png" alt=""></div>
                                </div>
                            </div>
                        </div>
                        <div ref="vanList" :style="{minHeight:vanListHeight+'px'}">
                            <div class="goodsBox" v-for="(goodsItem,goodsIndex) in goodsList"
                                 @click="goToProduct(goodsItem)" :key="'goodsIndex'+goodsIndex">
                                <div class="goodsItem">
                                    <div class="goodsImg">
                                        <!--                                    <img v-lazy="goodsItem.spuPicUrl" alt="" :key="goodsItem.spuPicUrl">-->
                                        <img :src="goodsItem.spuPicUrl" alt="">
                                        <div class="showActivitySpu" v-show="goodsItem.activitySpuId">秒杀</div>
                                        <div class="outSellBox" v-show="goodsItem.storeNumber==0&&!goodsItem.activitySpuId">补货中</div>
                                        <div class="jinshengNum" v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0&&!goodsItem.activitySpuId">仅剩{{goodsItem.storeNumber}}件</div>
                                    </div>
                                    <div class="goodsInfo">
                                        <div class="productInfo">
                                            <div class="goodsName">{{goodsItem.spuName}}</div>
                                            <div class="goodsDesc">{{goodsItem.spuSalePoint}}</div>
                                            <div class="goodsSale">月售{{goodsItem.showSale | threshold}}</div>
                                        </div>
                                        <div class="goodsPriceBx">
                                            <div class="goodsPriceInfo">
                                                <div class="goodsPrice">
                                                    <div class="goodsPriceIcon">￥</div>
                                                    <div class="goodsPriceNum">{{goodsItem.skuPrice|priceFilters}}</div>
                                                </div>
                                                <div class="goodsLinePrice" v-if="goodsItem.priceLine">
                                                    ￥{{goodsItem.priceLine|priceFilters}}
                                                </div>
                                            </div>
                                            <div class="addMinusBtn">
                                                <div class="clickBox" @click.stop="addGoodsNum(goodsItem,goodsIndex)"></div>
                                                <div class="cartNumBox" v-show="goodsItem.cartCount!=0" :class="goodsItem.cartCount>99?'cartNumBigBox':''">
                                                    <div class="cartNum">{{goodsItem.cartCount}}</div>
                                                </div>
                                                <img class="addBtn" ref="addBtn" src="@/assets/images/mall/mall_icon_add.png" alt="" @click.stop="addGoodsNum(goodsItem,goodsIndex)">
                                                <!-- <img class="addBtn" ref="addBtn" v-show="goodsItem.storeNumber!=0" src="@/assets/images/mall/mall_icon_add.png" alt="" @click.stop="addGoodsNum(goodsItem,goodsIndex)"> -->
                                                <!-- <img class="addBtn" v-show="goodsItem.storeNumber==0" src="@/assets/images/mall/mall_icon_add_gray.png" alt="" @click.stop="addGoodsNum(goodsItem,goodsIndex)"> -->
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="lineBox" v-show="goodsIndex != goodsList.length-1"></div>
                            </div>
                            <div class="goodsBlankBox" v-if="goodsList.length==0&&isloading">
                                <img src="../../../assets/images/default/no_shop_con_img.png" alt="">
                                <div>该分类无商品</div>
                            </div>
                        </div>
                        <!--                        ,paddingBottom:this.$stbHeight+'px'-->
                        <div class="finishedBox" ref="finishedBox" :style="{height:'43px',lineHeight:'43px'}" v-show="finished&&goodsList.length!=0">
                            <img src="@/assets/images/mall/mall_logo.png" alt="">
                        </div>
                        <div class="increasedBox" style="width: 100%;" :style="{height:shopCartHeight+'px'}"></div>
                    </div>
                    <!--满减、打包促销活动-->
                    <div v-else class="promotionPattern">
                        <!--打包满减模式-->
                        <div class="normalTitle" ref="normalTitle">
                            <div class="anchorList">
                                <div class="scrollX" ref="normalTitleScroll">
                                    <div  ref="anchorItem" v-for="(promotionClassItem,promotionClassIndex) in promotionGoodsList"
                                          :key="'promotionClassItem'+promotionClassIndex"  @click="setPromotionClassIndex(promotionClassIndex)">
                                        <div class="anchorItem" :class="promotionClassIndex==selectProClassIndex?'anchorItemAct':''">
                                            <div class="anchorName">{{promotionClassItem.actSlogan}}</div>
                                            <div class="anchorDesc">{{promotionClassItem.actTag}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="continueBtn" @click="setShowClassifyBox">
                                <img src="@/assets/images/mall/mall_icon_down.png" alt="">
                            </div>
                        </div>
                        <div class="reducedGoodsList" ref="reducedGoodsList" :class="isSideBarFixed?'classifyListFixed':''"
                             @scroll="reducedGoodsListScroll"
                             @touchstart="handlePromotionTouchStart"
                             @touchend="handlePromotionTouchEnd"
                             @touchmove="handlePromotionTouchMove">
                            <!--单个促销活动-->
                            <div class="reducedGoodsItem" ref="reducedGoodsItem" v-for="(promotionGoodsItem,promotionGoodsIndex) in promotionGoodsList"
                                 :key="'promotionGoodsIte'+promotionGoodsIndex">
                                <!--单个促销活动的头部信息-->
                                <div class="reducedGoodsTitle">
                                    <div class="classifyTxt">{{promotionGoodsItem.actSlogan}}</div>
                                    <div class="salePriceSort">
                                        <div class="saleSort" @click="promotionSaleSort(promotionGoodsItem)">
                                            <div>销量</div>
                                            <div class="redLineBox" v-show="promotionGoodsItem.sortNum==1"><img
                                                    src="@/assets/images/mall/redLine.png" alt=""></div>
                                        </div>
                                        <div class="priceSortBox" @click="promotionPriceSort(promotionGoodsItem)">
                                            <div class="priceSort">
                                                <div class="priceSortTxt">价格</div>
                                                <img :src="promotionGoodsItem.sortNum==0||promotionGoodsItem.sortNum==1?icon.filter:(promotionGoodsItem.sortNum==2?icon.filterUp:icon.filterDown)"
                                                     alt="">
                                            </div>
                                            <div class="redLineBox" v-show="promotionGoodsItem.sortNum==2||promotionGoodsItem.sortNum==3"><img
                                                    src="@/assets/images/mall/redLine.png" alt=""></div>
                                        </div>
                                    </div>
                                </div>
                                <!--单个促销活动的规则-->
                                <div class="labelBox">
                                    <div class="labelIcon" v-show="leftClassFlag==0">满减</div>
                                    <div class="labelIcon" v-show="leftClassFlag==1">打包</div>
                                    <div class="labelTxt" v-if="leftClassFlag==0">{{promotionGoodsItem.rulesTxt}}</div>
                                    <div class="labelTxt" v-if="leftClassFlag==1&&promotionGoodsItem.packageRule">{{promotionGoodsItem.packageRule.rulePrice}}元{{promotionGoodsItem.packageRule.ruleAmount}}件</div>
                                </div>
                                <!--单个促销活动的商品列表-->
                                <div class="reducedGoodsBox">
                                    <div class="reducedGoodsInfo" v-for="(promotionGoodsInfo,promotionGoodsInfoIndex) in promotionGoodsItem.skus"
                                         :key="'promotionGoodsInfo'+promotionGoodsInfoIndex" @click="goToProduct(promotionGoodsInfo)">
                                        <div class="goodsImg">
                                            <img :src="promotionGoodsInfo.skuPicUrl" alt="">
                                            <div class="outSellBox" v-show="promotionGoodsInfo.storeNumber==0">补货中</div>
                                            <div class="jinshengNum"  v-show="promotionGoodsInfo.storeNumber<=promotionGoodsItem.threshold&&promotionGoodsInfo.storeNumber!=0"
                                            >
                                                仅剩{{promotionGoodsInfo.storeNumber}}件
                                            </div>
                                        </div>
                                        <div class="goodsInfo">
                                            <div class="productInfo">
                                                <div class="goodsName">{{promotionGoodsInfo.spuName}}</div>
                                                <div class="goodsDesc">{{promotionGoodsInfo.spuSalePoint}}</div>
                                                <div class="goodsSale">月售{{promotionGoodsInfo.saleMonthCount | threshold}}</div>
                                            </div>
                                            <div class="goodsPriceBx">
                                                <div class="goodsPriceInfo">
                                                    <div class="goodsPrice">
                                                        <div class="goodsPriceIcon">￥</div>
                                                        <div class="goodsPriceNum">{{promotionGoodsInfo.priceLow|priceFilters}}</div>
                                                    </div>
                                                    <div class="goodsLinePrice" v-if="promotionGoodsInfo.priceLine">
                                                        ￥{{promotionGoodsInfo.priceLine|priceFilters}}
                                                    </div>
                                                </div>
                                                <div class="addMinusBtn">
                                                    <div class="clickBox" @click.stop="addGoodsNum(promotionGoodsInfo,promotionGoodsInfoIndex,promotionGoodsIndex)"></div>
                                                    <div class="cartNumBox" v-show="promotionGoodsInfo.cartCount!=null" :class="promotionGoodsInfo.cartCount>99?'cartNumBigBox':''">
                                                        <div class="cartNum">{{promotionGoodsInfo.cartCount}}</div>
                                                    </div>
                                                    <img class="addBtn" ref="addPromotionBtn" src="@/assets/images/mall/mall_icon_add.png" alt="" @click.stop="addGoodsNum(promotionGoodsInfo,promotionGoodsInfoIndex,promotionGoodsIndex)">
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <!--                            ,paddingBottom:this.$stbHeight+'px'-->
                            <div class="finishedBox" :style="{height:'43px',lineHeight:'43px'}" v-show="finished">
                                <img src="@/assets/images/mall/mall_logo.png" alt="">
                            </div>
                            <div class="increasedBox" style="width: 100%;" :style="{height:shopCartHeight+'px'}"></div>
                        </div>
                        <!--展示选择分类-->
                        <div class="fixedClassifyMaskBox" v-show="isShowClassifyBox"  @click="isShowClassifyBox = !isShowClassifyBox"></div>
                        <div class="fixedClassifyBox"  v-show="isShowClassifyBox">
                            <div class="fixedClassifyTitle">
                                <div class="fixedClassifyTxt">选择分类</div>
                                <div class="fixedClassifyIcon"  @click="isShowClassifyBox = !isShowClassifyBox"><img src="@/assets/images/mall/mall_icon_up.png" alt=""></div>
                            </div>
                            <div class="fixedAnchorList">
                                <div class="anchorItem" :class="fixedPromotionClassIndex==selectProClassIndex?'anchorItemAct':''"  v-for="(fixedPromotionClassItem,fixedPromotionClassIndex) in promotionGoodsList"
                                     :key="'fixedPromotionClassItem'+fixedPromotionClassIndex" @click="setPromotionClassIndex(fixedPromotionClassIndex)">
                                    <div class="anchorName">{{fixedPromotionClassItem.actSlogan}}</div>
                                    <div class="anchorDesc">{{fixedPromotionClassItem.actTag}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="shopCartMaskBox" v-show="shopCartData.actList.length!=0&&isShowShopCartMask"  @click="hideShopCartMask"></div>
        <div class="shopCartBox">
            <div class="freeShippingBox" ref="freeShippingBox" v-show="shopCartData.deliveryBaseLine!=0&&!isShowShopCartMask&&shopCartData.deliveryBaseLine!=null">
                <div class="freeShippingTxt">
                    <div v-show="shopCartData.shopSkuTotal-shopCartData.deliveryBaseLine>=0||shopCartData.delivery==0">已享受包邮</div>
                    <div v-show="shopCartData.shopSkuTotal-shopCartData.deliveryBaseLine<0&&shopCartData.delivery!=0">满{{shopCartData.deliveryBaseLine}}包邮</div>
                    <div class="shortMoney" v-show="shopCartData.deliveryBaseLine-shopCartData.shopSkuTotal>0&&shopCartData.skuCount!=0&&shopCartData.delivery!=0">还差￥{{(shopCartData.deliveryBaseLine-shopCartData.shopSkuTotal).toFixed(2)}}</div>
                </div>
            </div>
            <div class="shopCartGoodsBox" v-show="shopCartData.actList.length!=0&&isShowShopCartMask">
                <div class="shopCartGoodsTitle" v-show="shopCartData.deliveryBaseLine!=0&&shopCartData.deliveryBaseLine!=null">
                    <div v-show="shopCartData.shopSkuTotal-shopCartData.deliveryBaseLine>=0||shopCartData.delivery==0">已享受包邮</div>
                    <div v-show="shopCartData.shopSkuTotal-shopCartData.deliveryBaseLine<0&&shopCartData.delivery!=0">满{{shopCartData.deliveryBaseLine}}包邮</div>
                    <div class="shortMoney" v-show="shopCartData.deliveryBaseLine-shopCartData.shopSkuTotal>0&&shopCartData.skuCount!=0&&shopCartData.delivery!=0">还差￥{{(shopCartData.deliveryBaseLine-shopCartData.shopSkuTotal).toFixed(2)}}</div>
                </div>
                <div class="shopCartGoodsListBox">
                    <div class="shopCartGoodsListTitle">共{{shopCartData.skuCount}}件商品</div>
                    <div class="shopCartGoodsList">
                        <div class="activityGoodsList" v-for="(actItem,actIndex) in shopCartData.actList" :key="'actItem'+actIndex">
                            <div class="cutOffRule" v-show="actItem.isSecKill==2&&actIndex!=0"></div>
                            <div class="cutOffLine" v-show="actItem.actType!=null&&actIndex!=0||actItem.isSecKill==1&&actIndex!=0"></div>
                            <div class="labelBox" v-show="actItem.actType!=null">
                                <div class="labelIcon" v-show="actItem.actType==0||actItem.actType==1">满减</div>
                                <div class="labelIcon" v-show="actItem.actType==2">打包</div>
                                <div class="labelTxt">
                                    <div class="secksellMsg">{{actItem.actDesc}}</div>
                                </div>
                            </div>
                            <div class="activityGoodsBox" v-for="(skuItem,skuIndex) in actItem.skuList" :key="'skuItem'+skuIndex">
                                <div class="labelBox" v-show="skuItem.seckillFlag==0||skuItem.seckillFlag==1||skuItem.seckillFlag==4">
                                    <div class="labelIcon">秒杀</div>
                                    <div class="labelTxt">
                                        <div class="secksellMsg" v-show="skuItem.seckillFlag==0||skuItem.seckillFlag==4">该商品距离秒杀结束仅剩</div>
                                        <div class="secksellMsg" v-show="skuItem.seckillFlag==1">该商品距离秒杀开始还有</div>
                                        <van-count-down v-if="skuItem.seckillFlag==1" :auto-start="true" class="secksellTime" :time="skuItem.seckillFlag==1?(skuItem.seckillStartTime - skuItem.serverTime)*1000:''"
                                                        @finish="updateGoodsInfo" />
                                        <van-count-down v-if="skuItem.seckillFlag==0||skuItem.seckillFlag==4" :auto-start="true" class="secksellTime" :time="skuItem.seckillFlag==0||skuItem.seckillFlag==4?(skuItem.seckillEndTime - skuItem.serverTime)*1000:''"
                                                        @finish="updateGoodsInfo" />
                                    </div>
                                </div>
                                <div class="activityGoodsItem">
                                    <div class="activityGoodsImg">
                                        <img :src="skuItem.skuPicUrl" alt="">
                                    </div>
                                    <div class="activityGoodsInfo">
                                        <div class="activityGoodsName">{{skuItem.spuName}}</div>
                                        <div class="activityGoodsDesc">{{skuItem.attrValueContent}}</div>
                                        <div class="activityGoodsBottomInfo">
                                            <div class="activityGoodsPriceInfo">
                                                <div class="activityGoodsPrice">
                                                    <div>￥</div>
                                                    <div>{{skuItem.skuPrice}}</div>
                                                </div>
                                                <div class="activityGoodsLinePrice" v-show="skuItem.seckillFlag==0">￥{{skuItem.skuOldPrice}}</div>
                                            </div>
                                            <div class="amendNumBox">
                                                <div class="subtractBtn" @click.stop="updateShopCartGoodsNum(actItem,skuItem,false)">
                                                    <img src="@/assets/images/mall/mall_icon_subtract.png" alt="">
                                                </div>
                                                <div class="activityGoodsNum">{{skuItem.skuCount}}</div>
                                                <div class="addBtn" @click.stop="updateShopCartGoodsNum(actItem,skuItem,true)">
                                                    <img src="@/assets/images/mall/mall_icon_add.png" alt="">
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="outSecksellBox" v-if="skuItem.seckillFlag==4">
                                    <div class="outSecksellTitle"> 限{{skuItem.seckillLimit}}件 </div>
                                    <div class="outSecksellMsg">秒杀商品限购{{skuItem.seckillLimit}}件，超出将按原价购买该商品</div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
            <div class="shopCartContent" ref="shopCartContent" :class="isShowShopCartMask?'whiteBgShopCart':''" :style="{paddingBottom:this.$footBarHeight+'px'}">
                <div class="shopCartConTxt">
                    <div class="shopCartLeft" @click="callShop">
                        <div class="phoneImgIcon">
                            <img src="@/assets/images/newSlicing/Call_cut.png" alt="">
                        </div>
                        <div class="phoneTxt">联系商家</div>
                    </div>
                    <div class="shopCartRight" @click="showShopCartMask">
                        <div class="courierBox">
                            <div class="courierIcon">
                                <img src="@/assets/images/newSlicing/man_cut.png" alt="">
                                <div ref="cartIcon" class="cartNumBox"  :class="shopCartData.skuCount>99?'cartNumBigBox':shopCartData.skuCount==0?'optionCartNum':''">
                                    <div class="cartNum">{{shopCartData.skuCount>99?'99+':shopCartData.skuCount}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="shopCartCon">
                            <div class="shopCartPrice">
